@import "./common/global.scss";
@import "./common/normalize.scss";
@import "./common/tools.scss";
@import "./common/animate.scss";

@import "./includes/header.scss";

body {
  min-width: 1330px;
}

nav.menu.right {
  position: fixed;
  z-index: 66;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  a {
    display: block;
    margin-bottom: 1rem;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    border: 1px solid #fff;
    background: #fff;
    &.active {
      background: transparent
    }
  }
}

.section {
  position: relative;
  background-size: cover;
  .worktable {
    @include ct;
    .container {
      position: relative;
      max-width: 1600px;
      height: 100%;
      margin: 0 auto;
      padding: 0 20px;
      aside,
      article {
        float: left;
        width: 50%;
        top: 0;
        height: 100%;
      }
      aside {
        padding-top: 8%;
        position: relative;
      }
      article {
        padding-top: 13%;
        h2 {
          font-size: 1.1rem;
          line-height: 1.1rem;
          font-weight: 400;
          opacity: 0;
          span {
            margin-right: 0.5rem;
            padding-right: 0.25rem;
            position: relative;
            &:after {
              content: "";
              position: absolute;
              right: -8px;
              width: 2px;
              height: 50%;
              transform: translateY(-50%);
              top: 50%;
              background: #3ff8ff
            }
          }
        }
        .context {
          opacity: 0;
          font-size: 0.45rem;
          line-height: 0.6rem;
          font-weight: 100;
          margin-top: 1rem;
          text-align: justify;
        }   
      }
    }
  }
} 
 
.homepage {   
  background: #0a1026; // background:#0a1026 url("../../images//index/bg1.jpg") 50%;
  text-align: center;
  .worktable { 
    transform: perspective(500px);
    transform-style: preserve-3d;
  }    
  img { 
    max-width: 100%;
    transform-origin: 0 0;
    @include center;
  } 
  #round1 {
    animation: narrow 1.5s ease-in-out forwards;
  }
  #round2 {
    animation: spread 2s ease-out forwards;
  }
  #round3 {
    animation: spread .5s ease-in forwards;
  }
  #slogan {
    max-width: 95%;
    opacity: 0;
    animation: fadeuphome 2s .5s forwards;
  }
  #homelogo {
    opacity: 0;
    animation: fadedown 1.5s .5s forwards;
  }
  &.on{
    #round1{
      animation: rotateR 50s linear infinite
    }
    #round2 {
      animation: rotateL 30s linear infinite;
    }    
    #round3 {
      animation: rotateR 20s linear infinite;
    }    
  }
}

.energysave {
  background: url("/@@/images/index/bg2.jpg") 50%;
  .worktable {
    .container {
      aside {
        img {
          position: absolute;
          left: -10%
        }
      }
      article {
        ul {
          margin-top: 5%;
          @include cf;
          li {
            float: left;
            width: 25%;
            text-align: center;
            opacity: 0;
            div {
              margin: .3rem auto;
              width: 120px;
              height: 120px;
              border: 1px solid #3ff8ff;
              border-radius: 50%;
              line-height: 120px;
              b {
                font-size: 30px;
                margin-left: 5%
              }
            }
            span {
              font-size: 20px;
            }
            @for $i from 2 through 4 {
              &:nth-of-type(#{$i}) {
                animation-delay: (3+2*$i)*0.1s
              }
            }
          }
        }
      }
    }
  }
  &.on {
    h2 {
      animation: fadeleft .5s forwards;      
    }
    .context {
      animation: faderight .5s .2s forwards;      
    }
    ul li {
      animation: fadeup 1s .5s forwards;
    }
  }
}

.innovation {
  background: url("/@@/images/index/bg3.jpg") 50%;
  .worktable {
    .container {
      article {
        ul {
          @include cf;
          position: relative;
          z-index: 66;
          text-align: center;
          li {
            float: left;
            width: 25%;
            position: relative;
            opacity: 0;
            div {
              height: 2rem;
              margin: .5rem 0 .2rem;
            }
            p {
              font-size: 18px;
            }
            @for $i from 1 through 4 {
              &:nth-of-type(#{$i}) {
                animation-delay: ($i*2+3)*0.1s
              }
            }
          }
        }
        >div {
          opacity: 0;
          position: relative;
          padding: .4rem;
          background-color: #1a397f;
          margin-top: 1rem;
          border: 1px solid #69ddf8;
          font-size: .35rem;
          line-height: .5rem;
          color: #fff;
          transition: all .5s;
          .tri1,
          .tri2 {
            position: absolute;
            border-width: 14px;
            border-style: dashed dashed solid;
            font-size: 0;
            line-height: 0;
            border-color: transparent transparent #69ddf8;
            top: -28px;
            transition: all .5s;
            transform: translateX(-50%);
            left: 12.5%
          }
          .tri2 {
            top: -26px;
            border-color: transparent transparent #1a397f;
          }
          p {
            display: none;
          }
          p.on {
            display: block;
          }
        }
      }
      aside {
        img {
          position: absolute;
          right: -10%
        }
      }
    }
  }
  &.on {
    h2 {
      animation:  faderight .5s forwards;
    }
    .context {
      animation:  fadeleft .5s .2s forwards;
    }
    ul li {
      animation: fadeup 1s .5s forwards;
    }
    #fourcontent {
      animation: fadeup 1s 1.3s forwards;
    }
  }
}

.partner {
  background: url("/@@/images/index/bg4.jpg") 50%;
  .worktable {
    .container {
      aside {
        img {
          position: absolute;
          left: -10%
        }
      }
      article {
        ul {
          li {
            opacity: 0;
            float: left;
            width: 25%;
            margin-top: 1rem;
            @for $i from 1 through 12 {
              &:nth-of-type(#{$i}) {
                animation-delay: ($i*2+3)*0.1s
              }
            }
          }
        }
      }
    }
  }
  &.on {
    h2 {
      animation: fadeleft .5s forwards;      
    }
    .context {
      animation: faderight .5s .2s forwards;      
    }    
    ul li {
      animation: fadeup 1s .5s forwards;
    }
  }
}

.aboutus {  
  background: url("/@@/images/index/bg5.jpg") 50%;
  .worktable {
    .container {
      article {
        >p {
          line-height: .8rem;
        }
        ul {          
          li {
            opacity: 0;
            float: left;
            margin: 1rem 1rem 0 0;
            &:last-of-type{
              margin-right: 0;
              animation-delay: .7s;
            }
          }
        }
      }
      aside {
        img {
          position: absolute;
          right: -10%
        }
      }
    }
  }
  &.on{
    h2 {
      animation:  faderight .5s forwards;
    }
    .context {
      animation:  fadeleft .5s .2s forwards;
    }
    ul{
      li{animation: fadeup 1s .5s forwards;}
    }
  }
}

@media(max-width:1440px) {
  nav.menu.right {
    right: .2rem;
  }
  .section {
    .worktable {
      .container {
        max-width: 1330px;
        aside{
          width: 55%;
        }
        article {
          width: 45%;
          padding-top: 10%;
          h2 {
            font-size: 0.7rem;
            line-height: 0.8rem;
          }
          >p {
            font-size: 0.25rem;
            line-height: 0.4rem;
            margin-top: .8rem
          }
        }
      }
    }
  }
  .energysave,
  .partner {
    .worktable {
      .container {
        aside img {
          left: -10%
        }
      }
    }
  }
  .innovation,
  .aboutus {
    .worktable {
      .container {
        aside img {
          right: -10%
        }
      }
    }
  }
}

@media(max-width:1330px) {
  html {
    overflow-x: auto !important
  }
  nav#menu {
    right: .2rem;
  }
  .section {
    .worktable {
      .container {
        article {
          // padding-top: 13%;
          h2 {
            font-size: 0.7rem;
            line-height: 0.7rem;
          }
          >p {
            font-size: 0.28rem;
            line-height: 0.35rem;
            margin-top: .7rem
          }
        }
      }
    }
  }

}
  
@media(max-width:768px) {
  html {
    overflow-x: hidden !important;
    body {
      min-width: inherit;
      width: 100%;
    }
  }
  .section {
    .worktable {
      aside {
        display: none;
      }
      .container {
        article {
          float: none;
          width: 100%;
          padding-top: 13%;
          h2 {
            font-size: .7rem;
            line-height: .7rem;
            span {
              margin-right: 0.5rem;
              padding-right: 0.2rem;
            }
          }
          >p {
            margin-top: .5rem !important;
            font-size: 0.4rem;
            line-height: 0.45rem;
          }
        }
      }
    }
  }
  .homepage {
    #round1 {
      width: 150%;
      max-width: inherit;
    }
    #round3 {
      max-width: 80%;
    }
    #slogan {
      max-width: 80%;

    }    
    #homelogo{
      max-width: 70%
    }
  }
  nav.menu.right {
    right: .2rem;
  }
  .energysave {
    .worktable {
      .container {
        article {
          ul {
            margin-top: .8rem;
            li {
              width: 50%;
              margin-bottom: .4rem;
              div {
                margin: .3rem auto;
                width: 3rem;
                height: 3rem;
                line-height: 3rem;
                b {
                  font-size: .7rem
                }
              }
              span {
                font-size: .4rem;
              }
            }
          }
        }
      }
    }
    &.on {
      ul li {
        animation: fadeup 1s .5s forwards;
      }
    }
  }
  .innovation {
    .worktable {
      .container {
        article {
          ul {
            li {
              div {
                margin: .5rem 0 .2rem;
                img {
                  max-width: 70%;
                }
              }
              p {
                font-size: .3rem
              }
              @for $i from 1 through 4 {
                &:nth-of-type(#{$i}) {
                  animation-delay: ($i*2+3)*0.1s
                }
              }
            }
          }
        }
      }
    }
  }
  .partner {
    .worktable .container article {
      ul li {
        width: 33.33%;
      }
    }
  }
  .aboutus{
    .worktable .container article{
      ul li{
        text-align: center;
        width: 45%;
        margin: 1rem 10% 0 0;
        img{
          max-width: 85%
        }
      }
    }
  }
}

