/*animate */
@keyframes rotateR {
  0% {
    transform-origin: 0 0;
    transform: rotate(0deg) translate(-50%, -50%); }
  100% {
    transform-origin: 0 0;
    transform: rotate(360deg) translate(-50%, -50%); } }

@keyframes rotateL {
  0% {
    transform-origin: 0 0;
    transform: rotate(0deg) translate(-50%, -50%); }
  100% {
    transform-origin: 0 0;
    transform: rotate(-360deg) translate(-50%, -50%); } }

@keyframes spread {
  0% {
    opacity: 0;
    transform-origin: center;
    transform: translate(-50%, -50%) scale(0.2); }
  100% {
    opacity: 1;
    transform-origin: center;
    transform: translate(-50%, -50%) scale(1); } }

@keyframes narrow {
  0% {
    opacity: 0;
    transform-origin: center;
    transform: translate(-50%, -50%) scale(2); }
  100% {
    opacity: 1;
    transform-origin: center;
    transform: translate(-50%, -50%) scale(1); } }

@keyframes fadeup {
  0% {
    transform: translateY(20%);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fadeleft {
  0% {
    transform: translateX(-50%);
    opacity: 0; }
  100% {
    transform: translateX(0);
    opacity: 1; } }

@keyframes faderight {
  0% {
    transform: translateX(50%);
    opacity: 0; }
  100% {
    transform: translateX(0);
    opacity: 1; } }

@keyframes shake {
  0% {
    transform: translateY(5%) scale(1); }
  25% {
    transform: translateY(0%) scale(1); }
  50% {
    transform: translateY(-5%) scale(1.05); }
  75% {
    transform: translateY(0%) scale(1); }
  100% {
    transform: translateY(5%) scale(1); } }

@keyframes fadeuphome {
  0% {
    transform: translate(-50%, 50%);
    opacity: 0; }
  100% {
    transform: translate(-50%, -50%);
    opacity: 1; } }

@keyframes fadedown {
  0% {
    transform: translate(-50%, -60%);
    opacity: 0; }
  100% {
    transform: translate(-50%, -50%);
    opacity: 1; } }

header {
  position: fixed;
  width: 100%;
  z-index: 99;
  padding: .4rem 1rem;
  background: rgba(0, 0, 0, 0.1); }
  header nav {
    float: right; }
    header nav a {
      font-size: .28rem;
      font-weight: 100;
      margin: 0 0.22rem 0 0;
      position: relative;
      display: inline-block; }
      header nav a:after {
        content: " ";
        transition: all .5s;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 2px;
        background-color: transparent; }
      header nav a.active:after, header nav a:hover:after {
        transition: all .5s;
        background-color: #3ff8ff;
        bottom: -.15rem; }
    header nav #news {
      padding: 0.1rem .2rem;
      border-radius: .2rem;
      background-color: #0fa5e4;
      background-image: -moz-linear-gradient(128deg, #0fa5e4 0%, #288cc5 100%);
      background-image: -webkit-linear-gradient(128deg, #0fa5e4 0%, #288cc5 100%);
      background-image: -ms-linear-gradient(128deg, #0fa5e4 0%, #288cc5 100%);
      transition: all .5s; }
      header nav #news:after {
        display: none; }
      header nav #news:hover {
        background-image: -moz-linear-gradient(128deg, #14a1dc 0%, #1d628a 100%);
        background-image: -webkit-linear-gradient(128deg, #14a1dc 0%, #1d628a 100%);
        background-image: -ms-linear-gradient(128deg, #14a1dc 0%, #1d628a 100%); }

@media (max-width: 768px) {
