.homepage {
  background: #0a1026;
  text-align: center; }
  .homepage .worktable {
    transform: perspective(500px);
    transform-style: preserve-3d; }
  .homepage img {
    max-width: 100%;
    transform-origin: 0 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .homepage #round1 {
    animation: narrow 1.5s ease-in-out forwards; }
  .homepage #round2 {
    animation: spread 2s ease-out forwards; }
  .homepage #round3 {
    animation: spread .5s ease-in forwards; }
  .homepage #slogan {
    max-width: 95%;
    opacity: 0;
    animation: fadeuphome 2s .5s forwards; }
  .homepage #homelogo {
    opacity: 0;
    animation: fadedown 1.5s .5s forwards; }
  .homepage.on #round1 {
    animation: rotateR 50s linear infinite; }
  .homepage.on #round2 {
    animation: rotateL 30s linear infinite; }
  .homepage.on #round3 {
    animation: rotateR 20s linear infinite; }

